@import url(https://fonts.googleapis.com/css2?family=Poppins&display=swap);
body,
html {
  margin: 0;
  padding: 0;
}

#root{
  overflow-y: hidden;
}
.only-tab-div{
  display: none !important;
}
.preview-main-div {
  /* padding: 100px; */
  padding: 0px  15px;

}

.row-wrapper{
  display: flex;
  justify-content: space-between;
}

.left-div-new{
width: 50%;
}

.right-div-new{
width: 40%;
}

.previews-heading {
  /* margin-top: -300px; */
  margin-top: 15px;
}

.previews-heading .MuiTypography-body1 {
  font-size: 34px;
        font-weight: bolder;
        color: #04724d;
        text-shadow: 0px 1px, 1px 0px, 1px 1px;
        letter-spacing: 1.5px;
}
.previews-subHeading1 .MuiTypography-body1 {
  /* font: normal normal normal 25px/38px Popp */
  font-size:18px;
  opacity: 0.6;
}
.previews-subHeading .MuiTypography-body1 {
  font-size: 30px;
  color: #000000;
  opacity: 1;
  font-family: 'Poppins', sans-serif;
  font-weight: bold;
}

.turn-on-2{
  display: none;
}

.interview-video {
  position: relative;
}
.interview-video video {
  height: 360px;
  width: 100%;
  object-fit: cover;
  border-radius: 8px;
}

.page-content-preview {
  /* padding: 0 150px; */
  margin-top: 30px;
  /* display: flex; */
  /* justify-content: space-around; */
}
.preview-video {
  position: relative;
  background-color: black;
  height: 500px;
  border-radius: 30px;
}
.preview-video video {
  height: 500px;
  /* width: 700px; */
  object-fit: cover;
  border-radius: 25px;
}
.preview-start {
  margin: 70px 0px 0px 30px;
}

.Button1 {
  margin-top: 50px;
  background-color: #04724d;
  color: #ffffff;
}
.birbal-logo-preview2{
  display: none
}

.controls {
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: 0px;
  margin-bottom: 24px;
  left: 236px;
}
.videoHandle {
  padding: 0px;
  margin-right: 20px;
}
.candidateName {
  position: absolute;
  top: 0px;
  left: 25px;
  margin-top: 10px;
}
.candidateName span {
  margin-left: 10px;
}

