  padding-top: 10px;
}

@media (max-width: 1440px) {
  .preview-main-div {
    padding: 0px 0px 15px 50px;
  }
  .previews-heading .MuiTypography-body1 {
    font-size: 40px;
  }
  .page-content-preview {
    padding: 0px 20px;
  }
  .preview-video video {
    height: 585px;
    width: 625px;
  }
}

@media (max-width: 1268px) {
  .preview-main-div {
    padding: 0px 0px 15px 30px;
  }
}
