  .second-wrapper{
    padding: 0 20px 10px 20px;
    background: #fff;
    position: fixed;
    bottom: 0;
    width: 100%;
    }
    .access-text{
      font-size: 11px !important;
    }
    .tick-div,.tick-div-new{
      background: #fff !important;
    }
    .flex-div-preview{
      border: 1px solid #04724D57;
      padding: 10px 5px ;
      margin: 5px;
      border-radius: 4px;
      justify-content: center;
 
    }
    .interview-video{
      background: #fff;
      display: flex;
      justify-content: center;
    }
   .page-content-preview{
     margin: 0;
   }
