    display: none !important;
  }
  .only-tab-div{
    display: flex !important;
  }


  .page-content-preview{
    padding: 0;
    /* margin-top: 10px; */
  }
 
  .previews-heading .MuiTypography-body1{
    font-size: 24px;
  }
  .previews-subHeading1 .MuiTypography-body1{
    font-size: 12px;
  }
  .birbal-logo-preview{
    display: none !important;
  }
  .birbal-logo-preview2{
    display: block;
  }

  .turn-on-2{
    display: block;
    font-size: 15px;
    font-weight: 600;
  }
  .row-wrapper{
    flex-direction: column;
  }
  .left-div-new{
    width: 100%;
  }

  .right-div-new{
   display: none;
  }
