    margin: 0;
  }

  .audio-bar{
    display: none;
  }

  .audio-bar-2{
    display: block !important;
    width: 100%;
    padding: 20px 0;
  
  }
  .only-mobile-button{
    display: flex !important;
    justify-content: center;
    align-items: center;
  }
  .FirstModal{
    width: 90% !important;
    margin: 0 auto;
  }
  .dialog-wrapper{
    flex-direction: column;
  }
  .unstable-text-1{
    display: none;
  }
  .unstable-text-2{
    padding: 10px 0;
  }
