    padding: 10px !important;
    width: 60%;
    display: flex;
    justify-content: center;
    align-items: center;
  } */
  .right-div{
    width: 100%;
    display: flex;
    justify-content: flex-end;
  }
  .flex-div-preview-2{
    justify-content: space-between !important;
  }
   .div-1{
    width: 5% !important;
  }

   .div-2{
    width: 90% !important;
  }
