.Message {
  font-size: 30px;
  color: red;
  font-weight: bold;
}

.Enter {
  font-size: 30px;
  color: green;
  font-weight: bold;
}


.main-flex{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px; 
}

.main-flex-new{
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-top: 15px; 
}


.tick-div{
  width: 50%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #04724D17;
  border-radius: 4px;
  padding: 10px 0;
}

.tick-div-new{
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
  background: #04724D17;
  border-radius: 4px;
  padding: 10px 0;
}

.audio-bar{
  width: 45%;
  background: #04724D17;
  border-radius: 4px;
  padding: 10px 0;
}

.audio-bar-2{
  display: none;
}

.only-mobile-button{
  display: none;
}


.FirstModal {
  width: 600px;
  margin: auto 0;
  position: fixed;
  top: 10%;
 
}


.FirstModal-main {
  background: #fff;
  padding: 40px;
  display: flex;
  flex-direction: column;
  border-radius: 10px;

}

.message{
  font-size: 18px;
  font-weight: 600;
margin-top: 20px;
  text-align: center;
}

.icon-box{
  height: 20px;
  width: 20px;
  border-radius: 4px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex-div-preview{
  display: flex;
  justify-content: flex-start;
  align-items: center;
  width: 50%;
  padding-left: 20px;
}

.flex-div-preview-2{
  display: flex;
  justify-content:space-around;
  align-items: center;
}

.access-text{
  font-size: 14px;
  padding: 0 5px;
}

.div-1{
  width: 20%;
  display: flex;
  justify-content: flex-end;
}

.div-2{
  width: 70%;
}

.MuiDialog-container{
  height: auto !important;
  
}

.dialog-wrapper{
  display: flex;
  padding: 30px;
  border-radius: 15px;
  justify-content: space-between;
  align-items: center;
}
 .unstable-image{
   height: 40px;
   width: 40px;
 }

 .unstable-image img{
   width: 100%;
   height: 100%;
 }

 .unstable-text-1{
   font-size: 24px;
   font-weight: 700;
   font-family: 'Poppins', sans-serif;
   padding: 20px 0 10px 0;
 }

 .unstable-text-2{
   font-size: 14px;
   font-family: 'Poppins', sans-serif;
   opacity: 0.8;
 }

 .Modal-div{
   background: #04724d;
   color: #fff;
   font-family: 'Poppins', sans-serif;
   cursor: pointer;
   font-size: 12px;
   border-radius: 4px !important;
   padding: 10px !important;
   width: 60%;
   display: flex;
   justify-content: center;
   align-items: center;
 }

 .Modal-div-disabled{
  background: #ccc;
  color: #fff;
  font-family: 'Poppins', sans-serif;
  font-size: 12px;
  border-radius: 4px !important;
  padding: 10px !important;
  width: 60%;
  display: flex;
  justify-content: center;
  align-items: center;
}

 .MuiDialog-paperWidthSm{
   max-width: 800px !important;
   width: 800px;
 }


 @media(max-width:765px){
  
  .interview-video video {
    width: 360px;
    border-radius: 0px;
    object-fit: cover;
    height: 231px;
    object-position: center;
    }
  .main-flex-new{
    display: unset !important;
  }
  .only-tab-div{
    display: none !important;
  }
 }


 .SecondModal{
  width: 600px !important;
  margin: 0 auto;
}

.SecondModal .modal-body{
  background-color: #fff;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.SecondModal .mic-icon-div{
  width: 50px;
  height: 50px;
  background-color: #04724d;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  color: #fff;
  margin-bottom: 20px;
}

.SecondModal .audio-anayliser-div{
  margin-bottom: 20px;
}

.SecondModal .speak{

  margin-bottom: 20px;
  font-weight: 600;
  font-size: 13px;

}

.SecondModal .button-msg-wrapper{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.SecondModal .Modal-div{
  border-radius: 4px;
  padding: 5px 10px;
  font-size: 11px;
  width: 170px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.SecondModal .voice-message{
  font-size: 10px;
  padding: 10px 0;
  color: red;
}

